.mainbox{
    width: 100%;
    height: 90vh;
    /* background-color: greenyellow; */
    overflow: hidden;
}

.main{
    width: 100%;
    height: 90vh;
    /* background-color: red; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 750px 10% 0px;
    overflow-y: scroll  ;
}
.box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    /* background-color: yellow; */
    margin: 10px;
    width: 50%;
    border-radius: 10px;
    padding: 20px;
}
.box1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 350px;
    text-align: center;
}
.box1 p{
    font-size: 15px;
}
.box div{
    /* background-color: gray; */
}
.faqmain{
    /* background-color: red; */
    padding: 10px 150px;
    width: 100%;
}
.erorp{
    color: red;
    font-size: 12px;
    padding: 10px 0;
}

.data1{
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: red; */
    padding: 340px 40px;
}
.data2{
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: start;
    align-items: start;
    /* background-color: yellow; */
    flex-wrap: wrap;
}
.data2 div{
    width: 300px;
    height: fit-content;
    background-color: lightblue;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
}
.norecord{
    text-align: center;
    font-size: 30px;
